<template>
  <div class="CategoriesEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-select v-model="category.parent" placeholder="">
          <el-option v-for="item in parents" :key="item._id" :label="item.name" :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" v-model.trim="category.name" @blur="isShowAlert=true"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add" :disabled="formCheckFlag"></el-button>
      </el-form-item>
    </el-form>
    <el-alert
      :title="alertMsg"
      type="error"
      show-icon
      v-show="formCheckFlag && isShowAlert"
    ></el-alert>
  </div>
</template>
<script>
export default {
  name: "CategoriesEdit",
  data() {
    return {
      category: {
        _id: "",
        name: ""
      },
      parents: [],
      isShowAlert: false,
      alertMsg: ""
    };
  },
  props: {
    _id: String
  },
  computed: {
    // 
    formCheckFlag() {
      // 
      const regCategoryName = /^([a-zA-Z]|[\u4E00-\u9FA5]){2,16}$/;
      // 
      let { name } = this.category;
      // 
      if (!regCategoryName.test(name)) {
        this.alertMsg = "2-16";
        return true;
      }
      return false;
    }
  },
  methods: {
    // 
    async add() {
      // 
      let { name, parent } = this.category;
      // 
      let { _id } = this;
      if (!_id) {
        // 
        await this.$http.post("/categories", { name, parent }); //ajax
        //  
        this.$router.push("/categories/list");
      } else {
        // 
        await this.$http.put("/categories", { _id, name, parent });
        //  
        this.$router.push("/categories/list");
      }
    },
    // _id
    async getCategoryById() {
      // _id
      if (!this._id) {
        return;
      }
      let res = await this.$http.get(`/categories?_id=${this._id}`);
      this.category = res.data.data.result;
    },
    // 
    async getParents() {
      let res = await this.$http.get(`/categories?all=1`);
      this.parents = res.data.data.result;
    }
  },
  created() {
    this.getParents();
    this._id && this.getCategoryById();
  }
};
</script>
