// 
import Vue from "vue";
import Router from "vue-router";
import Main from "./views/Main";

// 
import CategoriesList from "./views/categories/CategoriesList";
import CategoriesEdit from "./views/categories/CategoriesEdit";

// 
import GoodsList from "./views/goods/GoodsList";
import GoodsEdit from "./views/goods/GoodsEdit";

// 
import HerosList from "./views/heros/HerosList";
import HerosEdit from "./views/heros/HerosEdit";

// 
import ArticlesList from "./views/articles/ArticlesList";
import ArticlesEdit from "./views/articles/ArticlesEdit";

// 
import AdvertsList from "./views/adverts/AdvertsList";
import AdvertsEdit from "./views/adverts/AdvertsEdit";

// 
import AdminUsersList from "./views/adminUsers/AdminUsersList";
import AdminUsersEdit from "./views/adminUsers/AdminUsersEdit";

// 
import Login from "./views/login/Login";

// 
import VideosList from "./views/videos/VideosList";
import VideosEdit from "./views/videos/VideosEdit";

// 
Vue.use(Router);

export default new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/Login",
      name: "Login",
      component: Login,
      meta: {
        isPublic: true //token
      }
    },
    {
      path: "/",
      redirect: "/main"
    },
    {
      path: "/main",
      name: "main",
      component: Main,
      children: [
        // 
        {
          path: "/categories/edit",
          name: "categoriesAdd",
          component: CategoriesEdit
        },
        {
          path: "/categories/edit/:_id",
          name: "categoriesEdit",
          component: CategoriesEdit,
          props:true
        },
        {
          path: "/categories/list",
          name: "categoriesList",
          component: CategoriesList
        },
        // 
        {
          path: "/goods/edit",
          name: "goodsAdd",
          component: GoodsEdit
        },
        {
          path: "/goods/edit/:_id",
          name: "goodsEdit",
          component: GoodsEdit,
          props:true
        },
        {
          path: "/goods/list",
          name: "goodsList",
          component: GoodsList
        },
        // 
        {
          path: "/heros/edit",
          name: "herosAdd",
          component: HerosEdit
        },
        {
          path: "/heros/edit/:_id",
          name: "herosEdit",
          component: HerosEdit,
          props:true
        },
        {
          path: "/heros/list",
          name: "herosList",
          component: HerosList
        },
        // 
        {
          path: "/articles/edit",
          name: "articlesAdd",
          component: ArticlesEdit
        },
        {
          path: "/articles/edit/:_id",
          name: "articlesEdit",
          component: ArticlesEdit,
          props:true
        },
        {
          path: "/articles/list",
          name: "articlesList",
          component: ArticlesList
        },
        // 
        {
          path: "/adverts/edit",
          name: "advertsAdd",
          component: AdvertsEdit
        },
        {
          path: "/adverts/edit/:_id",
          name: "advertsEdit",
          component: AdvertsEdit,
          props:true
        },
        {
          path: "/adverts/list",
          name: "advertsList",
          component: AdvertsList
        },
        // 
        {
          path: "/adminUsers/edit",
          name: "adminUsersAdd",
          component: AdminUsersEdit
        },
        {
          path: "/adminUsers/edit/:_id",
          name: "adminUsersEdit",
          component: AdminUsersEdit,
          props:true        //props:truepathpropsthis.$route.params._id--
        },
        {
          path: "/adminUsers/list",
          name: "adminUsersList",
          component: AdminUsersList
        },
        // 
        {
          path: "/videos/edit",
          name: "videosAdd",
          component: VideosEdit
        },
        {
          path: "/videos/edit/:_id",
          name: "videosEdit",
          component: VideosEdit,
          props:true
        },
        {
          path: "/videos/list",
          name: "videosList",
          component: VideosList
        }
      ]
    }
  ]
});
