<template>
  <div class="GoodsEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input
          placeholder=""
          v-model.trim="good.name"
          @focus="isShowAlert=false"
          @blur="isShowAlert=true"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUpload"
          :on-error="errorUpload"
          :headers="addHeaders"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="good.icon" :src="good.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add" :disabled="formCheckFlag"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="formCheckFlag && isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "GoodsEdit",
  data() {
    return {
      good: {
        _id: "",
        name: "",
        icon: ""
      },
      isShowAlert: false,
      alertMsg: ""
    };
  },
  props: {
    _id: String
  },
  computed: {
    // 
    formCheckFlag() {
      // 
      const regGoodName = /^([a-zA-Z]|[\u4E00-\u9FA5]){2,16}$/;
      // 
      let { name ,icon} = this.good;
      // 
      if (!regGoodName.test(name)) {      //
        this.alertMsg = "2-16";
        return true;
      }
      if(!icon){
        this.alertMsg = "";
        return true;
      }
      // 
      return false;
    }
  },
  methods: {
    // hander
    afterUpload(res) {
      //res
      this.good.icon = res.data.result.imgUrl;
    },
    // 
    async add() {
      // 
      let { name, icon } = this.good;
      // 
      let { _id } = this;
      if (!_id) {
        // ---ajax
        await this.$http.post("/goods", { name, icon });
        // 
        this.$router.push("/goods/list");
      } else {
        // ---ajax
        await this.$http.put("/goods", { _id, name, icon });
        // 
        this.$router.push("/goods/list");
      }
    },
    // _id
    async getgoodById() {
      let res = await this.$http.get(`/goods?_id=${this._id}`);
      this.good = res.data.data.result;
    }
  },
  created() {
    //created
    this._id && this.getgoodById(); //_id
  }
};
</script>
<style lang="scss" scoped>
.avatar-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>