<template>
  <div class="VideosEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-select v-model="video.category" placeholder="">
          <el-option
            v-for="category in categories"
            :key="category._id"
            :label="category.name"
            :value="category._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input placeholder="" v-model="video.title" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" v-model="video.playSort" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker v-model="video.createdAt" type="date" placeholder=""></el-date-picker>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUploadImg"
          :on-error="errorUpload"
          :headers="addHeaders"
          :before-upload="beforeImageUpload"
        >
          <img v-if="video.imgUrl" :src="video.imgUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUploadVideo"
          :on-error="errorUpload"
          :headers="addHeaders"
          :before-upload="beforeVideoUpload"
        >
          <!-- <img v-if="video.url" :src="video.url" class="avatar" /> -->
          <video controls v-if="video.url" class="avatar" ref="videoObj">
            <source :src="video.url" type="video/mp4" />
            <source :src="video.url" type="video/ogg" />Video
          </video>
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
import { videoParent } from "../../plugins/config";
export default {
  name: "VideosEdit",
  data() {
    return {
      video: {
        _id: "",
        title: "",
        url: "",
        imgUrl: "",
        category: "",
        playSort: 0,
        createdAt: new Date()
      },
      isShowAlert: false,
      alertMsg: "",
      categories: []
    };
  },
  props: {
    _id: String
  },
  methods: {
    // 
    afterUploadImg(res) {
      this.$set(this.video, "imgUrl", res.data.result.imgUrl);
    },
    // 
    afterUploadVideo(res) {
      this.$set(this.video, "url", res.data.result.imgUrl); //video
      this.$refs.videoObj.load(); //video
    },

    // 
    async add() {
      // 
      let { title, url, imgUrl, category } = this.video;
      title = title.trim();

      // 
      let { _id } = this;
      if (!_id) {
        // ---ajax
        await this.$http.post("/videos", { title, url, imgUrl, category });
        // 
        this.$router.push("/videos/list");
      } else {
        // ---ajax
        await this.$http.put("/videos", { _id, title, url, imgUrl, category });
        // 
        this.$router.push("/videos/list");
      }
    },
    // _id
    async getvideoById() {
      let res = await this.$http.get(`/videos?_id=${this._id}`);
      this.video = res.data.data.result;
    },
    // 
    async getVideoCategories() {
      let result = await this.$http.get(`/categories?parent=${videoParent}`); //
      this.categories = result.data.data.result;
    }
  },
  created() {
    //created
    this._id && this.getvideoById(); //_id
    this.getVideoCategories();
  }
};
</script>
<style lang="scss" scoped>
.avatar-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 378px;
  height: auto;
  display: block;
}
</style>