// js
import Vue from "vue";
import App from "./App.vue"; //
import router from "./router"; //
import store from "./store"; //
import "./plugins/element.js";

// 
import "./assets/css/common.scss";

// axiosVue
import http from "./http";
Vue.prototype.$http = http;

Vue.config.productionTip = false;

// mixin  ---
Vue.mixin({
  computed: {
    fileUploadAction() {
      //
      return http.defaults.baseURL + "/uploads";
    },
    addHeaders() {
      //headers
      return { Authorization: localStorage.getItem("token") };
    }
  },
  methods: {
    // handler
    beforeAvatarUpload(file) {
      const isFileTypeOk = /^image\/(jpeg|jpg|png)$/i.test(file.type);
      const isFileSizeOk = file.size / 1024 < 100;

      if (!isFileTypeOk) {
        this.$message.error(" jpeg|jpg|png !");
      }
      if (!isFileSizeOk) {
        this.$message.error(" 100KB!");
      }
      return isFileTypeOk && isFileSizeOk;
    },
    // handler
    beforeImageUpload(file) {
      const isFileTypeOk = /^image\/(jpeg|jpg|png)$/i.test(file.type);
      const isFileSizeOk = file.size / 1024 / 1024 < 2;

      if (!isFileTypeOk) {
        this.$message.error(" jpeg|jpg|png !");
      }
      if (!isFileSizeOk) {
        this.$message.error(" 2MB!");
      }
      return isFileTypeOk && isFileSizeOk;
    },
    // handler
    beforeVideoUpload(file) {
      const isFileTypeOk = /^video\/mp4$/i.test(file.type);
      const isFileSizeOk = file.size / 1024 / 1024 < 20;

      if (!isFileTypeOk) {
        this.$message.error(" mp4 !");
      }
      if (!isFileSizeOk) {
        this.$message.error(" 20MB!");
      }
      return isFileTypeOk && isFileSizeOk;
    },
    // handler
    errorUpload(err) {
      this.$message({
        type: "error",
        message: ` (${err.status})`
      });
    }
  }
});

// Vue
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
