<template>
  <div class="VideosList">
    <h1></h1>
    <el-table :data="videos" style="width: 100%" stripe fit>
      <el-table-column type="index" :index="fnIndex" label="" style="width:100px;"></el-table-column>
      <el-table-column prop="_id" label="_id" style="width:220px;"></el-table-column>
      <el-table-column prop="category.name" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="playSort" label=""></el-table-column>
      <el-table-column prop="createdAt" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/videos/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="primary" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <MyPagination
      :changeDatas="changeDatas"
      :changeTotal="changeTotal"
      :changeCurrPage="changeCurrPage"
      :pageSize="pageSize"
      ref="pagination"
      :apiName="apiName"
    ></MyPagination>
  </div>
</template>
<script>
import MyPagination from "../../components/MyPagination.vue";
import dayjs from "dayjs";
export default {
  name: "VideosList",
  data() {
    return {
      videos: [],
      total: 0,
      pageSize: 8,
      currPage: 1,
      apiName: "videos"
    };
  },
  components: {
    MyPagination
  },
  watch: {
    "videos"() {
      this.videos.map(video => {
        video.createdAt = dayjs(video.createdAt).format("YYYY-MM-DD");
      });
    }
  },
  methods: {
    fnIndex(index) {
      let { pageSize, currPage } = this;
      return 1 + (currPage - 1) * pageSize + index;
    },
    changeDatas(datas) {
      this.videos = datas;
    },
    changeTotal(total) {
      this.total = total;
    },
    changeCurrPage(currPage) {
      this.currPage = currPage;
    },

    del(row) {
      // 
      this.$confirm(`"${row.title}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // ajax
          await this.$http.delete(`/videos?_id=${row._id}`);
          // 
          this.$refs.pagination.currPageChange(this.currPage);
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>
