<template>
  <div class="AdminUsersEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model.trim="adminUser.username" @blur="isShowAlert=true" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          clearable
          show-password
          placeholder=""
          type="password"
          v-model.trim="adminUser.password"
          @blur="isShowAlert=true"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add" :disabled="formCheckFlag"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="formCheckFlag && isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "AdminUsersEdit",
  data() {
    return {
      adminUser: {
        _id: "",
        username: "",
        password: ""
      },
      alertMsg: "",
      isShowAlert:false
    };
  },
  props:{
    _id:{
      type:String
    }
  },
  computed: {
    // 
    formCheckFlag(){
      // 
      const regUser = /^[a-zA-Z0-9_-]{3,16}$/;
      const regPass = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Za-z])(?=.*[!@#$]).*$/;
      // 
      let { username, password } = this.adminUser;
      // 
      if(!regUser.test(username)){
        this.alertMsg = "3-16_-";
        return true
      }
      if(!regPass.test(password)){
        this.alertMsg = "6!@#$";
        return true
      }
      return false
    }
  },
  methods: {
    // 
    async add() {
      // 
      let { username, password } = this.adminUser;
      // 
      let { _id } = this
      if (!_id) {
        // 
        await this.$http.post("/adminUsers", { username, password });
        this.$router.push("/adminUsers/list");
      } else {
        // 
        await this.$http.put("/adminUsers", { _id, username, password });
        this.$router.push("/adminUsers/list");
      }
    },
    // _id
    async getAdminUserById() {
      let res = await this.$http.get(`/adminUsers?_id=${this._id}`);
      this.adminUser = res.data.data.result;
    }
  },
  created() {
    //created
    this._id && this.getAdminUserById(); //_id
  }
};
</script>
