<template>
  <div class="Login">
    <el-container>
      <el-form label-width="80px">
        <el-form-item label="">
          <el-input
            v-model="model.username"
            placeholder=""
            autofocus
            clearable
            @keyup.native.enter="login"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="model.password"
            placeholder=""
            type="password"
            @keyup.native.enter="login"
            show-password
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.native.prevent="login"></el-button>
        </el-form-item>
        <el-form-item label="">
          <template>
            <p> demo  demo</p>
          </template>
        </el-form-item>
      </el-form>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      model: {
        username: "",
        password: ""
      }
    };
  },

  methods: {
    async login() {
      //   
      let res;
      try {
        res = await this.$http.post("/login", this.model);
        localStorage.setItem("token", res.data.data.token); //token
        localStorage.setItem("username", res.data.data.username); //
        //   
        this.$router.push("/adminUsers/list");
        
      } catch (error) {
        this.model.username = this.model.password = ""
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.Login {
  width: 50vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #eee;
  padding: 20px;
  border-radius: 5px;
}
p {
  margin: 0;
  color: #999;
}
</style>