<template>
  <div class="ArticlesEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model="article.title" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="article.categories" multiple placeholder="">
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <vue-editor
          v-model="article.content"
          useCustomImageHandler
          @image-added="handleImageAdded"
          id="editor"
        ></vue-editor>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
import { VueEditor } from "vue2-editor"; //
import { articleParent } from "../../plugins/config";
export default {
  name: "ArticlesEdit",
  data() {
    return {
      article: {
        _id: "",
        title: "",
        categories: [],
        content: ""
      },
      isShowAlert: false,
      alertMsg: "",
      categories: []
    };
  },
  props: {
    _id: String
  },
  components: {
    VueEditor
  },
  methods: {
    // 
    async add() {
      // 
      let { title, categories, content } = this.article;
      title = title.trim();
      // 
      let { _id } = this;
      if (!_id) {
        // ---ajax
        await this.$http.post("/articles", { title, categories, content });
        // 
        this.$router.push("/articles/list");
      } else {
        // ---ajax
        await this.$http.put("/articles", { title, categories, content, _id });
        // 
        this.$router.push("/articles/list");
      }
    },
    // _id
    async getarticleById() {
      let res = await this.$http.get(`/articles?_id=${this._id}`);
      this.article = res.data.data.result;
    },
    // 
    async getCategories() {
      let result = await this.$http.get(`/categories?parent=${articleParent}`);
      this.categories = result.data.data.result;
    },

    // 
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      //  :return
      if (!this.beforeImageUpload(file)) {
        return;
      }
      // 
      var formData = new FormData();
      formData.append("file", file);
      // ajax
      let res = await this.$http.post(this.fileUploadAction, formData);
      // console.log(res.data.imgUrl)
      Editor.insertEmbed(cursorLocation, "image", res.data.data.result.imgUrl);
      resetUploader();
    }
  },
  created() {
    //created
    this.getCategories(); //
    this._id && this.getarticleById(); //_id
  }
};
</script>
