<template>
  <div class="AdminUsersList">
    <h1></h1>
    <el-table :data="adminUsers" style="width: 100%" stripe fit>
      <el-table-column prop="_id" label="_id" style="width:220px;"></el-table-column>

      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope" v-if="showOpt(scope.row.username)">
          <el-button
            :disabled="scope.row.username === 'admin' || scope.row.username === 'demo'"
            type="primary"
            size="small"
            @click="$router.push(`/adminUsers/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="primary" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "AdminUsersList",
  data() {
    return {
      adminUsers: []
    };
  },
  computed: {
    
  },
  methods: {
    //  admindemo
    showOpt(username){
      if(username === 'admin' || username === 'demo'){
        return false
      }
      return true
    },
    async getAdminUsers() {
      let res = await this.$http.get("/adminUsers");
      this.adminUsers = res.data.data.result;
    },
    del(row) {
      // 
      this.$confirm(`"${row.username}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // ajax
          let res = await this.$http.delete(`/adminUsers?_id=${row._id}`);
          // 
          this.getAdminUsers();
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    this.getAdminUsers();
  }
};
</script>
