<template>
  <div class="AdvertsList">
    <h1></h1>
    <el-table :data="adverts" style="width: 100%" stripe fit>
      <el-table-column prop="_id" label="_id" style="width:220px;"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/adverts/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="primary" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "AdvertsList",
  data() {
    return {
      adverts: []
    };
  },
  methods: {
    async getadverts() {
      let res = await this.$http.get("/adverts");
      this.adverts = res.data.data.result;
    },
    del(row) {
      // 
      this.$confirm(`"${row.name}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // ajax
          await this.$http.delete(`/adverts?_id=${row._id}`);
          // 
          this.getadverts();
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    this.getadverts();
  }
};
</script>
